import { parseQuestionBank } from '@/utils/parser';
import { validateQuestionBank } from '@/utils/validator';

export const readFileContent = file =>
  new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = event => resolve(event.target.result);
    reader.onerror = () => reject(new Error('文件读取失败'));
    reader.readAsText(file, 'utf-8');
  });

export const importQuestionFile = async file => {
  if (!file) {
    throw new Error('请选择题库文件');
  }
  const content = await readFileContent(file);
  return buildBankFromContent(content, file.name);
};

export const buildBankFromContent = (content, fileName = '自定义题库.json') => {
  const parsed = JSON.parse(content);
  validateQuestionBank(parsed);
  return parseQuestionBank(content, fileName);
};

