const judgeOptions = [
  { key: '正确', label: '正确' },
  { key: '错误', label: '错误' },
];

const normalizeOptions = (options, type) => {
  if (type === 'judge') {
    return judgeOptions;
  }
  if (typeof options === 'object' && !Array.isArray(options)) {
    return Object.entries(options || {}).map(([key, label]) => ({
      key,
      label,
    }));
  }
  if (Array.isArray(options)) {
    return options
      .map(item => {
        if (typeof item === 'string') {
          return { key: item, label: item };
        }
        return {
          key: item.key || item.id || item.value || item.K || '',
          label: item.label || item.value || item.name || item.L || '',
        };
      })
      .filter(item => item.key);
  }
  return [];
};

const normalizeType = data => {
  const hasOptionData =
    (Array.isArray(data.options) && data.options.length) ||
    (data.O && Object.keys(data.O).length);

  if (!hasOptionData) {
    return 'judge';
  }

  const answerSource = data.correctAnswer ?? data.R;
  if (Array.isArray(answerSource)) {
    return 'multiple';
  }
  if (typeof answerSource === 'string' && answerSource.includes(',')) {
    return 'multiple';
  }

  return data.type || 'single';
};

const normalizeAnswer = (answer, type, optionKeys = []) => {
  if (type === 'multiple') {
    const payload = Array.isArray(answer)
      ? answer
      : String(answer || '')
          .split(',');

    return payload
      .map(item => (item || '').trim())
      .filter(Boolean)
      .sort();
  }

  const pickKeyByIndex = rawIndex => {
    if (!optionKeys.length) return null;
    const index = Number(rawIndex);
    if (Number.isNaN(index)) return null;
    return optionKeys[index] || optionKeys[index - 1] || null;
  };

  if (typeof answer === 'number') {
    return pickKeyByIndex(answer) || String(answer).trim();
  }

  if (Array.isArray(answer)) {
    const first = answer[0];
    return (
      pickKeyByIndex(first) ||
      pickKeyByIndex(Number(first)) ||
      String(first || '').trim()
    );
  }

  if (answer === undefined || answer === null) {
    return '';
  }

  const trimmed = String(answer).trim();
  return pickKeyByIndex(trimmed) || trimmed;
};

export class Question {
  constructor(data) {
    this.id = data.I || data.id;
    this.question = data.Q || data.question || '';
    this.type = normalizeType(data);
    this.options = normalizeOptions(
      data.options && Object.keys(data.options).length ? data.options : data.O,
      this.type
    );
    const optionKeys = this.options.map(item => item.key);
    this.correctAnswer = normalizeAnswer(
      data.correctAnswer ?? data.R,
      this.type,
      optionKeys
    );
    this.explanation = data.X || data.explanation || '';
    this.category = data.category || '综合';
    this.level = data.level || 1;
    this.userAnswer = data.userAnswer || (this.type === 'multiple' ? [] : null);
    this.isCorrect = typeof data.isCorrect === 'boolean' ? data.isCorrect : null;
    this.isAnswered = Boolean(data.isAnswered);
  }
}

