import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import Vant from 'vant';
import 'vant/lib/index.css';
import '@/assets/css/base.scss';
import { saveState, mapStateToStorage } from '@/services/storageService';

Vue.config.productionTip = false;
Vue.use(Vant);

store.dispatch('questions/hydrateFromStorage');
store.subscribe((_, state) => {
  saveState(mapStateToStorage(state));
});

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');

