import { Question } from '@/models/question';

export const buildLevels = questions => {
  const size = 10;
  const total = Math.ceil(questions.length / size) || 1;
  return Array.from({ length: total }).map((_, index) => {
    const start = index * size;
    const end = Math.min((index + 1) * size, questions.length);
    return {
      id: `level-${index + 1}`,
      title: `第${index + 1}关`,
      start,
      end,
      unlocked: index === 0,
      threshold: 80,
    };
  });
};

export const parseQuestionBank = (content, fallbackTitle = '自定义题库') => {
  let payload = JSON.parse(content);

  if (Array.isArray(payload)) {
    payload = {
      id: `bank-${Date.now()}`,
      title: fallbackTitle,
      description: '自定义导入题库',
      questions: payload,
    };
  }

  if (!payload.questions || !Array.isArray(payload.questions)) {
    throw new Error('题库格式不正确，缺少 questions 数组');
  }

  const questions = payload.questions.map((item, index) => {
    const enriched = { ...item, level: item.level || Math.floor(index / 10) + 1 };
    return new Question(enriched);
  });

  return {
    id: payload.id || `bank-${Date.now()}`,
    title: payload.title || fallbackTitle,
    description: payload.description || '',
    total: questions.length,
    questions,
    levels: buildLevels(questions),
  };
};

