const initialState = () => ({
  levelMap: {},
  history: [],
  lastResult: null,
});

const state = initialState();

const getters = {
  unlockedLevels: state =>
    Object.entries(state.levelMap)
      .filter(([, value]) => value.unlocked)
      .map(([key]) => key),
  lastResult: state => state.lastResult,
};

const mutations = {
  RESET_PROGRESS(state, payload) {
    state.levelMap = {};
    payload.levels.forEach((level, index) => {
      state.levelMap[level.id] = {
        unlocked: index === 0,
        bestAccuracy: 0,
        attempts: 0,
      };
    });
    state.history = [];
    state.lastResult = null;
  },
  RECORD_HISTORY(state, record) {
    state.history = [record, ...state.history].slice(0, 20);
    state.lastResult = record;
  },
  UPDATE_LEVEL(state, { levelId, accuracy }) {
    const current = state.levelMap[levelId];
    if (!current) return;
    current.attempts += 1;
    if (accuracy > current.bestAccuracy) {
      current.bestAccuracy = accuracy;
    }
  },
  UNLOCK_LEVEL(state, levelId) {
    if (state.levelMap[levelId]) {
      state.levelMap[levelId].unlocked = true;
    }
  },
  HYDRATE(state, payload) {
    if (!payload) return;
    state.levelMap = payload.levelMap || {};
    state.history = payload.history || [];
    state.lastResult = payload.lastResult || null;
  },
};

const actions = {
  resetProgress({ commit }, { levels }) {
    commit('RESET_PROGRESS', { levels });
  },
  recordAnswer() {
    // 预留钩子：未来可用于实时统计
  },
  completeLevel({ commit, state }, { levelId, result }) {
    commit('UPDATE_LEVEL', { levelId, accuracy: result.accuracy });
    const record = {
      levelId,
      accuracy: result.accuracy,
      total: result.total,
      correct: result.correct,
      timestamp: Date.now(),
    };
    commit('RECORD_HISTORY', record);
    const nextLevelId = `level-${Number(levelId.split('-')[1]) + 1}`;
    if (state.levelMap[nextLevelId] && result.accuracy >= 80) {
      commit('UNLOCK_LEVEL', nextLevelId);
    }
  },
  hydrate({ commit }, payload) {
    commit('HYDRATE', payload);
  },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};

