export const isValidQuestion = question =>
  question &&
  typeof question.I !== 'undefined' &&
  typeof question.Q === 'string' &&
  typeof question.R !== 'undefined';

export const validateQuestionBank = payload => {
  if (!payload) {
    throw new Error('题库内容为空');
  }

  if (!Array.isArray(payload.questions) && !Array.isArray(payload)) {
    throw new Error('题库缺少 questions 数组');
  }

  const questions = payload.questions || payload;
  if (!questions.length) {
    throw new Error('题库没有任何题目');
  }

  const invalid = questions.find(item => !isValidQuestion(item));
  if (invalid) {
    throw new Error(`题目 ${invalid.I || invalid.id} 缺少必要字段`);
  }
};

