import { importQuestionFile, buildBankFromContent } from '@/services/importService';
import { buildLevels } from '@/utils/parser';
import { checkAnswer, buildResult } from '@/services/quizService';
import { loadState } from '@/services/storageService';
import { Question } from '@/models/question';

const initialState = () => ({
  questionBank: null,
  levels: [],
  currentLevelId: null,
  currentQuestions: [],
  currentIndex: 0,
  wrongQuestions: [],
});

const state = initialState();

const getters = {
  hasBank: state => Boolean(state.questionBank),
  bankInfo: state => state.questionBank,
  levels: state => state.levels,
  currentLevel: state => state.levels.find(level => level.id === state.currentLevelId),
  currentQuestion: state => state.currentQuestions[state.currentIndex] || null,
  questionProgress: state => ({
    current: state.currentIndex + 1,
    total: state.currentQuestions.length,
  }),
  wrongQuestions: state => state.wrongQuestions || [],
};

const mutations = {
  RESET_STATE(state) {
    Object.assign(state, initialState());
  },
  SET_QUESTION_BANK(state, payload) {
    state.questionBank = payload;
    state.levels = payload.levels || [];
    state.currentLevelId = state.levels[0]?.id || null;
  },
  SET_CURRENT_LEVEL(state, levelId) {
    state.currentLevelId = levelId;
    state.currentIndex = 0;
    state.currentQuestions = [];
  },
  SET_CURRENT_QUESTIONS(state, questions) {
    state.currentQuestions = questions;
    state.currentIndex = 0;
  },
  SET_CURRENT_INDEX(state, index) {
    state.currentIndex = index;
  },
  SET_USER_ANSWER(state, { index, answer, isCorrect }) {
    const target = state.currentQuestions[index];
    if (!target) return;
    target.userAnswer = target.type === 'multiple' ? [...answer] : answer;
    target.isCorrect = isCorrect;
    target.isAnswered = true;
  },
  RECORD_WRONG(state, question) {
    state.wrongQuestions = [
      ...state.wrongQuestions.filter(item => item.id !== question.id),
      question,
    ];
  },
  HYDRATE_WRONG_QUESTIONS(state, questions) {
    state.wrongQuestions = questions.map(item => new Question(item));
  },
  CLEAR_WRONG_QUESTION(state, questionId) {
    state.wrongQuestions = state.wrongQuestions.filter(item => item.id !== questionId);
  },
  CLEAR_ALL_WRONG_QUESTIONS(state) {
    state.wrongQuestions = [];
  },
};

const actions = {
  async importQuestionBank({ commit, dispatch }, file) {
    const bank = await importQuestionFile(file);
    commit('RESET_STATE');
    commit('SET_QUESTION_BANK', bank);
    dispatch(
      'progress/resetProgress',
      { levels: bank.levels },
      { root: true }
    );
    return bank;
  },
  async importQuestionContent({ commit, dispatch }, { content, fileName }) {
    const bank = buildBankFromContent(content, fileName);
    commit('RESET_STATE');
    commit('SET_QUESTION_BANK', bank);
    dispatch(
      'progress/resetProgress',
      { levels: bank.levels },
      { root: true }
    );
    return bank;
  },
  hydrateFromStorage({ commit, dispatch }) {
    const cached = loadState();
    if (!cached?.questionBank) {
      return;
    }
    const parsed = {
      ...cached.questionBank,
      questions: cached.questionBank.questions.map(item => new Question(item)),
    };
    parsed.levels = parsed.levels?.length ? parsed.levels : buildLevels(parsed.questions);
    commit('SET_QUESTION_BANK', parsed);
    if (cached.wrongQuestions && Array.isArray(cached.wrongQuestions)) {
      commit('HYDRATE_WRONG_QUESTIONS', cached.wrongQuestions);
    }
    dispatch('progress/hydrate', cached.progress, { root: true });
    dispatch('user/hydrate', cached.user, { root: true });
  },
  loadLevelQuestions({ state, commit }, levelId) {
    const level =
      state.levels.find(item => item.id === levelId) || state.levels[0];
    if (!level) {
      return [];
    }
    commit('SET_CURRENT_LEVEL', level.id);
    const questions = state.questionBank.questions
      .slice(level.start, level.end)
      .map(item => new Question(item));
    commit('SET_CURRENT_QUESTIONS', questions);
    return questions;
  },
  submitAnswer({ state, commit, dispatch }, answer) {
    const current = state.currentQuestions[state.currentIndex];
    const normalizedAnswer =
      current.type === 'multiple' ? [...answer].sort() : answer;
    const isCorrect = checkAnswer(current, normalizedAnswer);
    commit('SET_USER_ANSWER', {
      index: state.currentIndex,
      answer: normalizedAnswer,
      isCorrect,
    });
    if (!isCorrect) {
      commit('RECORD_WRONG', { ...current });
    }
    dispatch(
      'progress/recordAnswer',
      {
        levelId: state.currentLevelId,
        isCorrect,
      },
      { root: true }
    );
    return isCorrect;
  },
  goNext({ state, commit }) {
    if (state.currentIndex < state.currentQuestions.length - 1) {
      commit('SET_CURRENT_INDEX', state.currentIndex + 1);
    }
  },
  goPrev({ state, commit }) {
    if (state.currentIndex > 0) {
      commit('SET_CURRENT_INDEX', state.currentIndex - 1);
    }
  },
  finishLevel({ state, dispatch }) {
    const result = buildResult(state.currentQuestions);
    dispatch(
      'progress/completeLevel',
      {
        levelId: state.currentLevelId,
        result,
      },
      { root: true }
    );
    return result;
  },
  loadWrongQuestions({ state }) {
    return state.wrongQuestions || [];
  },
  loadWrongQuestionsForPractice({ state, commit }) {
    if (!state.wrongQuestions || !state.wrongQuestions.length) {
      return [];
    }
    const questions = state.wrongQuestions.map(item => new Question(item));
    commit('SET_CURRENT_QUESTIONS', questions);
    commit('SET_CURRENT_LEVEL', 'wrong-questions');
    return questions;
  },
};

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
};

