const STORAGE_KEY = 'learning-hero-state';

const safeParse = value => {
  try {
    return JSON.parse(value);
  } catch (error) {
    return null;
  }
};

export const loadState = () => {
  if (typeof window === 'undefined') return null;
  const raw = window.localStorage.getItem(STORAGE_KEY);
  return safeParse(raw);
};

export const saveState = state => {
  if (typeof window === 'undefined') return;
  window.localStorage.setItem(STORAGE_KEY, JSON.stringify(state));
};

export const clearState = () => {
  if (typeof window === 'undefined') return;
  window.localStorage.removeItem(STORAGE_KEY);
};

export const mapStateToStorage = storeState => ({
  questionBank: storeState.questions.questionBank,
  wrongQuestions: storeState.questions.wrongQuestions,
  progress: storeState.progress,
  user: storeState.user,
});

