<template>
  <div id="app" class="app-shell">
    <van-nav-bar
      fixed
      safe-area-inset-top
      title="学习英雄-引导式学习"
      left-arrow
      @click-left="handleBack"
    />
<main class="app-shell__content page-spacing">
      <router-view />
    </main>
    <van-tabbar route safe-area-inset-bottom>
      <van-tabbar-item replace to="/" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item replace to="/import" icon="upgrade">导入</van-tabbar-item>
      <van-tabbar-item replace to="/quiz" icon="apps-o">闯关</van-tabbar-item>
      <van-tabbar-item replace to="/settings" icon="setting-o">设置</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted() {
    this.initKeyboardShortcuts();
  },
  beforeDestroy() {
    this.removeKeyboardShortcuts();
  },
  methods: {
    handleBack() {
      if (this.$route.path === '/') {
        return;
      }
      this.$router.back();
    },
    initKeyboardShortcuts() {
      this.keyboardHandler = (event) => {
        const { key, ctrlKey, metaKey } = event;
        const isModifier = ctrlKey || metaKey;

        // Ctrl/Cmd + 1: 首页
        if (isModifier && key === '1') {
          event.preventDefault();
          if (this.$route.path !== '/') {
            this.$router.push('/');
          }
          return;
        }

        // Ctrl/Cmd + 2: 错题回顾
        if (isModifier && key === '2') {
          event.preventDefault();
          const wrongCount = this.$store.getters['questions/wrongQuestions']?.length || 0;
          if (wrongCount === 0) {
            this.$toast('暂无错题');
            return;
          }
          if (this.$route.path !== '/wrong') {
            this.$router.push('/wrong');
          }
          return;
        }

        // Ctrl/Cmd + 3: 闯关
        if (isModifier && key === '3') {
          event.preventDefault();
          if (this.$route.path !== '/quiz') {
            this.$router.push('/quiz');
          }
          return;
        }
      };
      document.addEventListener('keydown', this.keyboardHandler);
    },
    removeKeyboardShortcuts() {
      if (this.keyboardHandler) {
        document.removeEventListener('keydown', this.keyboardHandler);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.app-shell {
  min-height: 100vh;
  padding-top: 46px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: transparent;
}

.app-shell__content {
  flex: 1;
  padding: 16px 20px 72px;
  width: 100%;
}

.van-nav-bar {
  width: 100%;
  max-width: 640px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(8px);
}

.van-tabbar {
  box-shadow: 0 -8px 24px rgba(15, 23, 42, 0.08);
}
</style>

