const initialState = () => ({
  nickname: '学习英雄',
  autoShowExplanation: true,
  enableHint: false,
  theme: 'light',
});

const state = initialState();

const mutations = {
  UPDATE_SETTING(state, { key, value }) {
    state[key] = value;
  },
  HYDRATE(state, payload) {
    Object.assign(state, initialState(), payload || {});
  },
};

const actions = {
  updateSetting({ commit }, payload) {
    commit('UPDATE_SETTING', payload);
  },
  hydrate({ commit }, payload) {
    commit('HYDRATE', payload);
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};

