import { formatMultipleAnswer } from '@/utils/helper';

export const checkAnswer = (question, answer) => {
  if (!question) return false;
  if (question.type === 'multiple') {
    return formatMultipleAnswer(answer) === formatMultipleAnswer(question.correctAnswer);
  }
  return answer === question.correctAnswer;
};

export const buildResult = questions => {
  const total = questions.length;
  const correct = questions.filter(item => item.isCorrect).length;
  const wrongQuestions = questions.filter(item => item.isCorrect === false);
  const accuracy = total ? Math.round((correct / total) * 100) : 0;
  return {
    total,
    correct,
    accuracy,
    wrongQuestions,
  };
};

